<template>
  <div id="overall-card-component">
    <div class="left-container">
      <div v-if="type === 'pie'">
        pie chart
      </div>
      <div v-if="type === 'growth'">
        {{ isPositive ? "up chart" : "down chart" }}
      </div>
    </div>
    <div class="right-container">
      <h3 class="title">{{ title }}</h3>
      <div class="value">{{ percentValue }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed } from "vue";

const Component = defineComponent({
  name: "OverallCard",
  props: {
    type: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    },
    value: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const { value } = toRefs(props);

    const isPositive = computed(() => value.value >= 0);
    const percentValue = computed(() => {
      return `${isPositive.value ? "+" : "-"} ${Math.abs(value.value)}%`;
    });

    return {
      isPositive,
      percentValue
    };
  }
});

export default Component;
</script>

