<template>
  <div class="avatar-component" :style="componentStyle">
    <font-awesome-icon class="icon" :icon="['fas', 'user']"></font-awesome-icon>
  </div>
</template>

<script>
import { computed, toRefs } from "vue";
export default {
  name: "Avatar",
  props: {
    size: {
      type: Number,
      default: 100
    }
  },
  setup(props) {
    const { size } = toRefs(props);
    const componentStyle = computed(() => ({
      width: `${size.value}px`,
      height: `${size.value}px`,
      fontSize: `${size.value * 0.4}px`
    }));

    return {
      componentStyle
    };
  }
};
</script>

<style lang="stylus" scoped>
.avatar-component
  flex()
  border 1px solid $color-black
  border-radius 50%
  background white
</style>
